import React, {useCallback, useEffect, useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import {restoreState} from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'
 type DateType = {
    data: Date
 }
function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))

    const [show, setShow] = useState<boolean>(false)


    const start = useCallback(() => {
 let id = setInterval(() => {
            setDate(new Date());
        }, 1000);
setTimerId(+id)
   
        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)

    },[])

    const stop = useCallback(() => {
        if(timerId){
            clearInterval(timerId);
            setTimerId(undefined)

        }
            //   //    ,    (timerId <- undefined)


        }
    ,[timerId])

    const onMouseEnter = () => { //   //     
setShow(true)
    }
    const onMouseLeave = () => { //   //      
setShow(false)
    }

    const days =["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
const months= [
        "January", "February", "March", "April", "May", "June", "July",
        "August", "September", "October", "November", "December"
    ]



    const stringTime =  date.toLocaleTimeString()
    const stringDate = date.toLocaleDateString('ru-RU') || <br/> // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const stringDay = days[date.getDay()] || <br/> //  
    const stringMonth = months[date.getMonth()]  || <br/> //  

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong >{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    disabled={timerId? true : false} //   //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    disabled={timerId === undefined ? true : false} //   //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
